import axios from 'axios'

// axios.defaults.baseURL = 'https://api.108350.top/api/admin'
axios.defaults.baseURL = 'https://api.dqjjysm.top/api/admin'

export const web_url = 'https://api.dqjjysm.top/api/admin'
export const login = (data) => {
  return axios({
    url: '/record',
    method: 'post',
    data: data
  })
}
export const loginqq = (data) => {
  return axios({
    url: '/loginqq',
    method: 'post',
    data: data
  })
}
export const getShareMode = (data) => {
  return axios({
    url: '/getShareMode',
    method: 'get',
    params: data
  })
}
export const getCounter = (data) => {
  return axios({
    url: '/getCounter',
    method: 'get',
    params: data
  })
}
export const postSSFN = (data) => {
  return axios({
    url: '/postSSFN',
    method: 'post',
    params: data
  })
}



