<template>
  <div class="nav">
    <div class="nav_con">
            <a class="logo2" href="/" target="_blank">绝地求生官方网站</a>
            <ul class="clearfix">
                <li  style="cursor: pointer" @click="jump('login')"><i></i><a><b>老兵</b><span>账号绑定</span></a></li>
                <li  style="cursor: pointer" @click="jump('pubg1')"><i></i><a><b>福利1</b><span>老兵专属服装</span></a></li>
                <li  style="cursor: pointer" @click="jump('pubg2')"><i></i><a><b>福利2</b><span>清夏浴袍套装</span></a></li>
                <li  style="cursor: pointer" @click="jump('pubg3')"><i></i><a><b>福利3</b><span>丛林乱斗法则</span></a></li>
            </ul>
            <div class="nav_links">
                <a class="bbs" href="javascript:;"><i></i>论坛</a>
                <a class="ry" href="javascript:;"><i></i>荣誉<b></b></a>
            </div>
            <div class="denglu">
                <!--登录前状态 Start-->
                <p id="unlogin">亲爱的用户，请 <a :href="'/#/login?agent='+agent" id="dologin">[登录]</a></p>
                <!--登录前状态 End-->
                <!--登录后状态 Start-->
                <p id="logined" style="display:none">欢迎您，<span id='login_qq_span'></span>，<a href="javascript:;" id="dologout">[注销]</a></p>
                <!--登录后状态 End-->
            </div>
            <a href="//pubg.qq.com/main.shtml" class="btn_enter" title="进入官网" onclick="PTTSendClick('link','enter','进入官网');">进入官网</a>
        </div>
    </div>
</template>

<script>
export default {
  name: "headerNav",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      agent: this.$route.query.agent?this.$route.query.agent:0
      }
    },
    mounted: function() {
    },
    methods: {
      jump($page){
        this.$router.push("/"+$page+"?agent="+this.agent+"&page="+$page)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



