<template>
  <div class="pubg5">
    <pubg>
      <template slot="active">
        <active5></active5>
      </template>
    </pubg>
  </div>
</template>

<script>
import pubg from '@/pages/pubg'
import active5 from '@/components/active5'
import {web_url} from "../api/api";

export default {
  name: 'pubg5',
  components: {
    pubg: pubg,
    active5
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted: function() {
    let url = web_url+'/'
    let meta = document.querySelectorAll("meta");
    meta[2].setAttribute('content','PUBG万圣狂欢 -万圣节套装领取 - steam官网')
    meta[3].setAttribute('content',url+'/images/5.jpg')
    meta[4].setAttribute('content','PUBG万圣狂欢 -万圣节套装领取 - steam官网')
    document.title = 'PUBG万圣狂欢 -万圣节套装领取 - steam官网'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @media screen and (max-width: 1200px) {
    .p3_bg1 {
      height: 500px;
    }
    a.p3_bg1_a2 {
      top: 450px;
    }
    .pg3 .navmt76 {
      float: left;
    }
  }
</style>



