import Vue from 'vue'
import Router from 'vue-router'
import pubg1 from '@/pages/pubg1'
import pubg2 from '@/pages/pubg2'
import pubg3 from '@/pages/pubg3'
import pubg4 from '@/pages/pubg4'
import pubg5 from '@/pages/pubg5'
import lol1 from '@/pages/lol1'
import pgi from '@/pages/pgi'
import pgi2 from '@/pages/pgi2'
import pgi3 from '@/pages/pgi3'
import loginSteam from '@/pages/loginSteam'
import share1 from '@/assets/images/share1.jpg'

Vue.use(Router)

const router =  new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: 'pgi',
      meta: {
        title: '登录'
      }
    },
    {
      name: 'pubg1',
      path: '/pubg1',
      component: pubg1,
      meta: {
        title: '老兵空降回归-绝地求生官方网站-腾讯游戏',
        name: '老兵空降回归-绝地求生官方网站-腾讯游戏',
        description: '老兵空降回归-绝地求生官方网站-腾讯游戏',
        image: share1,
        keywords: '绝地求生,绝地求生官网,绝地求生激活码,绝地求生下载,绝地求生公测,绝地求生资格,绝地求生,腾讯绝地求生,吃鸡,今晚吃鸡,晚上吃鸡,大吉大利,PUBG,PUBG官网,PUBG社区'
      }
    },
    {
      name: 'pubg2',
      path: '/pubg2',
      component: pubg2,
      meta: {
        title: '清凉一夏,领浴袍服装-绝地求生官方网站-腾讯游戏'
      }
    },
    {
      name: 'pubg3',
      path: '/pubg3',
      component: pubg3,
      meta: {
        title: '丛林乱斗,领取丛林限定服饰-绝地求生官方网站-腾讯游戏'
      }
    },
    {
      name: 'pubg4',
      path: '/pubg4',
      component: pubg4,
      meta: {
        title: '预约报名-绝地求生官方网站-腾讯游戏'
      }
    },
    {
      name: 'pubg5',
      path: '/pubg5',
      component: pubg5,
      meta: {
        title: 'PUBG万圣狂欢 -万圣节套装领取 - steam官网'
      }
    },
    {
      name: 'pgi',
      path: '/pgi',
      component: pgi,
      mata: {
        title: '《绝地求生》-STEAM版本官方网站'
      }
    },
    {
      name: 'lol1',
      path: '/lol1',
      component: lol1,
      mata: {
        title: '阿卡丽的神秘商店'
      }
    },
    {
      name: 'pgi2',
      path: '/pgi2',
      component: pgi2,
      mata: {
        title: '《绝地求生》-STEAM版本官方网站'
      }
    },
    {
      name: 'pgi3',
      path: '/pgi3',
      component: pgi3,
      mata: {
        title: '《绝地求生》-STEAM版本官方网站'
      }
    },
    {
      name: 'login',
      path: '/login',
      component: loginSteam,
      meta: {
        title: '登录'
      }
    }
  ]
})
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
export default router



