<template>
  <div class="pubg3">
    <pubg>
      <template slot="active">
        <active3></active3>
      </template>
    </pubg>
  </div>
</template>

<script>
import pubg from '@/pages/pubg'
import active3 from '@/components/active3'
import {web_url} from "../api/api";
export default {
  name: 'pubg3',
  components: {
    pubg: pubg,
    active3
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted: function() {
    let url = web_url+'/'
    let meta = document.querySelectorAll("meta");
    meta[2].setAttribute('content','丛林乱斗,领取丛林限定服饰-绝地求生官方网站-腾讯游戏')
    meta[3].setAttribute('content',url+'/images/3.jpg')
    meta[4].setAttribute('content','丛林乱斗,领取丛林限定服饰-绝地求生官方网站-腾讯游戏')
    document.title = '丛林乱斗,领取丛林限定服饰-绝地求生官方网站-腾讯游戏'
  }
}
</script>

