<template>
  <div class="pubg2">
    <pubg>
      <template slot="active">
        <active2></active2>
      </template>
    </pubg>
  </div>
</template>

<script>
import pubg from '@/pages/pubg'
import active2 from '@/components/active2'
import {web_url} from "../api/api";

export default {
  name: 'pubg2',
  components: {
    pubg: pubg,
    active2
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted: function() {
    let url = web_url+'/'
    let meta = document.querySelectorAll("meta");
    meta[2].setAttribute('content','清凉一夏,领浴袍服装-绝地求生官方网站-腾讯游戏')
    meta[3].setAttribute('content',url+'/images/2.jpg')
    meta[4].setAttribute('content','清凉一夏,领浴袍服装-绝地求生官方网站-腾讯游戏')
    document.title = '清凉一夏,领浴袍服装-绝地求生官方网站-腾讯游戏'
  }
}
</script>

