<template>
  <div class="pubg1">
    <pubg>
      <template slot="active">
        <active1></active1>
      </template>
    </pubg>
  </div>
</template>

<script>
import pubg from '@/pages/pubg'
import active1 from '@/components/active1'
import {web_url} from "../api/api";

export default {
  name: 'pubg1',
  components: {
    pubg: pubg,
    active1
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted: function() {
    let url = web_url+'/'
    let meta = document.querySelectorAll("meta");
    meta[2].setAttribute('content','老兵空降回归-绝地求生官方网站-腾讯游戏')
    meta[3].setAttribute('content',url+'/images/1.jpg')
    meta[4].setAttribute('content','老兵空降回归-绝地求生官方网站-腾讯游戏')
    document.title = '老兵空降回归-绝地求生官方网站-腾讯游戏'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



