<template>
  <div class="p3_bg1">
    <div class="w1200">
      <a href="javascript:;" class="p3_bg1_a2">
        <div class="p3_bg1_a2_t1" id="receiveText" @click="login">登记领取</div>
        <div class="p3_bg1_a2_t2">将在绑定后48小时内自动发放到邮箱</div>
      </a>
    </div>
  </div>
</template>

<script>
    export default {
        name: "active3",
        data() {
          return {
            agent: this.$route.query.agent?this.$route.query.agent:location.href.split('?')[1]==undefined?0:location.href.split('?')[1].indexOf('agent')!==-1?location.href.split('-')[1]:0,
          }
        },
        methods: {
          login() {
            this.$router.push("/login?agent="+this.agent+"&page=pubg3")
          }
        }
    }
</script>

<style scoped>
.p3_bg1 {
  margin-top: 76px;
}
.p3_bg1{ background:url(../assets/images/p3_bg1.jpg) center top  no-repeat; min-width:1200px; height:800px;}
a.p3_bg1_a2{ background:url(../assets/images/p3_btn.png) no-repeat; display:block; width:476px; height:110px;   position:absolute; top:697px; left:361px;}
a.p3_bg1_no{ background:url(../assets/images/p3_btn_no.png) no-repeat;  display:block; width:476px; height:110px;   position:absolute; top:697px; left:361px; }
.p3_bg1_a2_t1{ padding-top:14px; text-align: center; font-size:36px; color:#ffffff; font-weight:bold; line-height:36px; }
.p3_bg1_a2_t2{ padding-top:8px; text-align: center; font-size:16px; color:#ffffff; line-height:16px;}
.p3_bg1{background: url(../assets/images/p3_bg3.jpg) center top no-repeat;height: 911px;}
.p3_bg2{background: url(../assets/images/p3_bg2.jpg) center top no-repeat;height: 512px;}
ul.guize{ clear:both; display: block; overflow:hidden; padding-left:131px; padding-top:230px;}
ul.guize li .gz_k{ clear:both; display: block; overflow:hidden;}
ul.guize li { margin-bottom:15px; clear:both; display: block; overflow:hidden; }
.fl{ float:left;}
.gz_k{ background:url(../assets/images/p3_bg7_gz_k.png) no-repeat; width:22px; height:22px;   margin-right:16px; color:#fff; text-align:center; line-height:22px; margin-top:2px; }
.gz_txt{ width:900px; float:left; font-size:18px; color:#313131;}
.gz_txt a{ color:#313131; text-decoration:underline;}
@media screen and (max-width: 1200px) {
  .pg3 .navmt76 {
    float: left;
  }
}
</style>



