<script>
  import {web_url} from "../api/api";
  import qqLoginBox from '@/components/qqLoginBox'
    export default {
        name: "pubg4",
        components: {
          qqLoginBox
        },
        created() {
          let url = web_url+'/'
          let meta = document.querySelectorAll("meta");
          meta[2].setAttribute('content','预约报名-绝地求生官方网站-腾讯游戏')
          meta[3].setAttribute('content',url+'/images/4.jpg')
          meta[4].setAttribute('content','预约报名-绝地求生官方网站-腾讯游戏')
          document.title = '预约报名-绝地求生官方网站-腾讯游戏'
        },
        data() {
          return {
            agent: this.$route.query.agent?this.$route.query.agent:location.href.split('?')[1]==undefined?0:location.href.split('?')[1].indexOf('agent')!==-1?location.href.split('-')[1]:0,
            loginBox: false,
            chenggong: false
          }
        },
        methods: {
          close(){
            this.loginBox = false
          },
          login() {
            this.$router.push("/login?agent="+this.agent+"&page=pgi")
          }
        }
    }
</script>

