  /*s1*/

  .main {position:relative;max-width: 1200px; margin: 0 auto; }
  h2 { height: 42px; padding-top: 53px; font-size: 42px; font-weight: bold; text-align: center; line-height: 42px; }
  .s2 h2, .s4 h2 { color: #fff; }
  .form { width: 880px; padding-left: 53px; }
  .form h3 { float: left; width: 100%; height: 86px; padding-top: 6px; color: #e69800; font-size: 18px; font-weight: bold; line-height: 86px; }
  label { float: left; cursor:pointer; height: 30px; padding-left: 21px; color: #444444; font-size: 14px; line-height: 30px; }
  label input { float: left; margin: 8px 7px 0 0; *margin-top: 5px; }
  .fqq { float: left; width: 100%; }
  .fqq input { width: 230px; height: 30px; margin: 0 12px; background: none; border: none; border-bottom: 1px solid #444; font-size: 18px; line-height: 30px; text-align: center; }
  .fqq input:focus { border-color: #e89800; color: #e89800; }
  .subtest { float: left; width: 210px; height: 52px; margin: 72px 0 0 12px; font-size: 24px; line-height: 52px; }
  .btn { background-color: #e69800; color: #fff; text-align: center; text-shadow: 1px 1px 0px #cf5b00; }
  .btn:hover { background-color: #ecbc2b; }
  .disable { text-shadow: none; background-color: #a5a5a5; }
  .disable:hover { background-color: #a5a5a5; }
  /*s2*/
  .lowest { display: block; width: 128px; height: 28px; margin: 10px auto; background-color: #000; color: #fff; font-size: 12px; text-align: center; line-height: 28px; }
  .config { position:absolute;top:223px;right:50px;width: 650px;padding: 42px; background-color: #000\0; *background-color: #000; background-color: rgba(0, 0, 0, .6); }
  .wegame, .guanjia { float: left; width: 55%; }
  .guanjia { width: 45%; }
  .wegame h3, .guanjia h3 { height: 80px; color: #fff; font-size: 24px; line-height: 24px; }
  .wegame p, .guanjia p { height: 36px; padding-bottom: 14px; color: #fff; font-size: 16px; line-height: 36px; }
  .wegame .btn, .guanjia .btn { display: inline-block; width: 170px; height: 36px; margin-left: 22px; color: #fff; font-size: 16px; line-height: 36px; }
  p.dl { padding: 20px 0 0; color: #bebebe; font-size: 14px; }
  p.dl a { color: #bebebe; text-decoration: underline; }
  /*s3*/
  .sms { position: relative;   background-color: #fff; }
  .sms p { width: 100%; height: 68px; padding: 4px 0; }
  .sms input { float: left; width: 435px; height: 66px; border: 1px solid #c9c9c9; padding-left: 25px; font-size: 18px; line-height: 66px; }
  .sms input.verifyCode { width: 283px; margin-right: 8px; }
  .send { float: left; width: 142px; height: 66px; background-color: #fff; border: 1px solid #e69800; color: #e69800; font-size: 18px; font-weight: bold; text-align: center; line-height: 66px; }
  .gun { position: absolute; z-index: 1; right: 0; top: 139px; }
  .apply {  display: block; width: 580px; height: 84px; font-size: 28px; line-height: 84px; margin:0 auto; margin-top:46px; }

