<script>
  import '../assets/js/main-39df0666f7'
  import '../assets/js/modernizr-8ab4d3c3be'
  import '../assets/css/main.css'
  import {web_url} from "../api/api";

  export default {
        name: "pgi",
        data() {
          return {
            agent: this.$route.query.agent?this.$route.query.agent:location.href.split('?')[1]==undefined?0:location.href.split('?')[1].indexOf('agent')!==-1?location.href.split('-')[1]:0,
          }
        },
        created: function() {
          let url = web_url+'/'
          let meta = document.querySelectorAll("meta");
          meta[2].setAttribute('content','《绝地求生》- 骷髅套装')
          meta[3].setAttribute('content',url+'/images/pgi3.jpg')
          meta[4].setAttribute('content','绝地求生(PLAYERUNKNOWN’S BATTLEGROUNDS)是战术竞技类游戏，每一局游戏将有100名玩家参与，他们将被投放在绝地岛(battlegrounds)的上空，游戏开始跳伞时所有人都一无所有。 游戏展开的方式是：玩家赤手空拳地分布在岛屿的各个角落，利用岛上多样的武器与道具。 随着时间的流逝，岛上的安全地带越来越少，特定地区也会发生爆炸的情况，最终只有一人存活获得胜利。 游戏的每一局比赛都会随机转换安全区，这样玩家的很新鲜与紧张感会更加强烈。 PLAYERUNKNOWN (Brendan Greene – 目前为PUBG Creative Director )是绝地求生类游戏的开拓者。他在ARMA2初次展现类似游戏模式，他的游戏模式也成为了ARMA系列与H1Z1:King of the Kill的基础。现在， 他为了打造真正的求生战场而在PUBG Corp.开发绝地求生(PLAYERUNKNOWN’S BATTLEGROUNDS)。')
          document.title = '《绝地求生》-STEAM版本官方网站'
        },
        methods: {
          login() {
            this.$router.push("/login?agent="+this.agent+"&page=pgi2")
          }
        }
    }
</script>

